





import { component, prop, Styles } from '@pyro/platform';
import Vue from 'vue';

@component({
    delimiters: ['\{\{\{', '}}}']
})
export default class Form extends Vue {
    @prop.classPrefix('form') classPrefix: string

    get classes() {
        return {
            [ this.classPrefix ]: true
        }
    }

    get style(): Styles {
        return {}
    }
}
